export const addMember = async ({ commit }, personPreviewData) => {
    try {
        commit("LOADING",true);
        const response  = await Api.post("/add-member", personPreviewData)
        if(response.status == 200) {
            console.log('33',response.data);
            commit("SET_UID",response.data.uid);
            commit("SET_SUCCESS_MESSAGE",'Member addd successfully');
            commit("LOADING",false);
        }
     
    } catch (error) {
        console.log('addMember action',error)
        commit("LOADING",false);
        commit("ERROR_PAGE_SHOW",true);
    }

    commit("LOADING",false);
}