<template>
  <section :style="styling" v-if="isValid" class="photo-capture fullAreaCapture">
    <video v-show="showVideo" ref="player" class="camera" autoplay playsinline />
<!--    <figcaption>Left-Side</figcaption>-->
    <canvas v-show="!showVideo" class="preview" ref="canvas" />
    <!-- <h1>TESTING</h1> -->
    <div class="center photo-capture-actions" >
 <div  v-if="showVideo">
     <button
         class="btn flex-center"
         @click.prevent="capture"
     ><i class="fa fa-camera" ></i></button>
      <button @click.prevent="changeCamera" class="camera_switch" 
     ><i class="fa fa-refresh"></i></button>

     <div class="bottom-message base">
  <span><i class="fa fa-info-circle" aria-hidden="true"></i>&nbsp;&nbsp; 枠に収まるように撮影をしてください。</span>
     </div>
 </div>
      <div v-else>
          <div class="bottom-message white">
             <div> <span> 写真がブレたり、見切れている場合は<br>
再撮影をしてください。</span></div>

              <button :class="'btn '+ buttonsClasses" @click.prevent="cancel">{{cancelBtnContent}}</button>
              <button :class="'btn '+ buttonsClasses" @click.prevent="done">{{doneBtnContent}}</button>
          </div>
      </div>
    </div>
  </section>
</template>

<script>
const constraints = {
    video: {
      width: {
        min: 0,
        ideal: 1920,
        max: 2560,
      },
      height: {
        min: 0,
        ideal: 1080,
        max: 1440,
      },
    },
  }

export default {
  name: "PhotoCapture",
  props: {
    styling: {
      type: Object,
      isRequired: false
    },
    value: {
      default: null
    },
    hideButtons: {
      type: Boolean,
      default: false
    },
    buttonsClasses: {
      type: String,
      default: ""
    },
    captureBtnContent: {
      //default: "Capture"
    },
    cancelBtnContent: {
      default: "再撮影する"
    },
    doneBtnContent: {
      default: "これを使う"
    },
     nxtStep: {
      type: Function
    },
    step: {
      type: String,
    },
    useFrontCameraProps:{
      type: Boolean
    }


  },
  // props: ['web_cam_open','step'],
  data() {
    return {
      showVideo: true,
      picture: null,
      isValid: true,
      stepCamera: '',
      useFrontCamera: false
    };
  },
  mounted() {
    this.videoPlayer = this.$refs.player;
    this.canvasElement = this.$refs.canvas;
    //
    this.stepCamera = this.step
     this.useFrontCamera = this.useFrontCameraProps
   console.log('104',  this.stepCamera)
      console.log('105',  this.useFrontCamera)

    this.streamUserMediaVideo();

  },
 
  methods: {
    streamUserMediaVideo() {
      if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
        return;
      }
      this.stopVideoStream();
      // constraints.video.facingMode = this.useFrontCamera ? "user" : "environment";
      constraints.video.facingMode = this.useFrontCamera ? "environment" : "user";
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(stream => (this.videoPlayer.srcObject = stream))
        .catch(() => {
          this.isValid = false;
        });
    },
    //switch camera
  changeCamera () {
    this.useFrontCamera = !this.useFrontCamera;
    console.log('81',this.useFrontCamera);
    this.streamUserMediaVideo();
  },
    capture() {
      this.showVideo = false;
      this.canvasElement.width = this.videoPlayer.videoWidth;
      this.canvasElement.height = this.videoPlayer.videoHeight;

      var context = this.canvasElement.getContext("2d");
      context.translate(this.canvasElement.width, 0);
      context.scale(-1, 1);

      context.drawImage(this.$refs.player, 0, 0);

      this.stopVideoStream();
      this.picture = this.$refs.canvas.toDataURL();
    },
    done() {
      this.$emit("input", this.picture);
      this.showVideo = true;
      this.streamUserMediaVideo();
      // this.nxtStep()
      if(this.stepCamera == "icardFront"){
        console.log('1526666666666');
      } else if(this.stepCamera == "icardBack"){
        console.log('1544444444');
      } else if(this.stepCamera == "idcard_side"){
         console.log('156');
      }

    },
    cancel() {
      this.showVideo = true;
      this.streamUserMediaVideo();
    },
    stopVideoStream() {
      //  console.log('128',this.$refs.player , this.$refs.player.srcObject)
      if (!(this.$refs.player && this.$refs.player.srcObject)) return;
      // console.log('130',this.$refs.player , this.$refs.player.srcObject)
      this.$refs.player.srcObject.getVideoTracks().forEach(track => {
        track.stop();

      });
    },
   
  }
};
</script>

<style scoped>
video {
    /* -webkit-transform: scaleX(-1); */
    /* transform: scaleX(-1); */
  }
canvas{
  -webkit-transform: scaleX(-1);
}
.camera_switch{
    top: -45px;
    left: 28px;
    border: none;
    color: white;
    border-radius: 50%;
    background-color: #4DB0A9;
    position: relative;
    width: 25px;
    height: 25px;
}
</style>


