/*System Importing*/
import Vue from 'vue'
import VueRouter from 'vue-router'

/*Custom Components Importing*/
import Home from '../components/pages/Home'

import PersonalInfo from "../components/pages/PersonalInfo";
import PersonalInfoPreview from "../components/pages/PersonalInfoPreview";
import PersonalSuccessMessage from "../components/pages/PersonalSuccessMessage";
import DocumentType from "../components/pages/DocumentType";
import IdcardFront from "../components/pages/IdcardFront";
import IdcardBack from "../components/pages/IdcardBack";
import IdcardSide from "../components/pages/IdcardSide";

import Video1 from "../components/pages/Video1";




/*Inserting VueRouter to the Vue Framework*/
Vue.use(VueRouter);


const routes = [

    {path: '/dd', component: Home, name: 'Home'},
  
    {path: '/personal-information/', component: PersonalInfo, name: 'PersonalInfo'},
    {path: '/personal-information-preview/', component: PersonalInfoPreview, name: 'PersonalInfoPreview'},
    {path: '/personal-information-success/', component: PersonalSuccessMessage, name: 'PersonalSuccessMessage'},
    {path: '/', component: DocumentType, name: 'DocumentType'},
    {path: '/idcard-front', component: IdcardFront, name: 'IdcardFront'},
    {path: '/idcard-back', component: IdcardBack, name: 'IdcardBack'},
    {path: '/ff', component: IdcardSide, name: 'IdcardSide'},

    
    {path: '/video1/', component: Video1, name: 'Video1'},

];

export const router = new VueRouter({
    mode: 'history', //hides hash in url after localhost:8080
    routes: routes
});
